/*        */
h1,
h2,
h3,
h4,
h5,
h6,
p {
    margin: 0;
}

/*        (ul) */
.list {
    list-style: none;
    padding-left: 0;
    margin: 0;
}

/*        */
.link {
    text-decoration: none;
    color: inherit;
}

/*      */
img {
    display: block;
    max-width: 100%;
    height: auto;
}

/*      address */
address {
    font-style: normal;
}